import './ControllerConfigPanel.css';
import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";

export function ControllerConfigPanel(props) {
    const [isCollapseMenu, setIsCollapseMenu] = useState(true);
    const [validateStep, setValidateStep] = useState([false, false, false, false, false, false]);
    const navigate = useNavigate();

    function handleLinkStep(e) {
        let baliseTarget;
        if(e.target.tagName !== 'P') {
            switch (e.target.tagName) {
                case 'SPAN':
                    baliseTarget = e.target.parentNode;
                    break;
                case 'B':
                    baliseTarget = e.target.parentNode.parentNode;
                    break;
            }
        } else {
            baliseTarget = e.target;
        }


        const targetStep = parseInt(baliseTarget.dataset.stepSave);

        if (props.configPart === 1 && validateStep[targetStep - 1]) {
            if (targetStep < 6) {
                props.saveStep(targetStep, true);
            } else if (targetStep === 5) {
                props.saveMap();
            } else {
                props.saveMap();
                navigate('/edit-session/' + props.currentConfig.config._id);
            }
        } else if(props.configPart === 2) {
            console.log(props.currentConfig)
            console.log('/edit-config/' + props.currentConfig.setup._id)
            navigate('/edit-config/' + props.currentConfig.setup._id);
            props.saveMap(false);
        }
    }

    useEffect(() => {
        if (props.configStep === 1)
            return;

        const cpyValidateStep = [...validateStep];
        cpyValidateStep[props.configStep - 2] = true;
        setValidateStep(cpyValidateStep);

    }, [props.configStep]);

    useEffect(() => {
        if (props.isEditConfig)
            setValidateStep(validateStep.map(step => true));
    }, []);

    return <div className={"controller-panel-container" + (!isCollapseMenu ? ' controller-is-open' : '')}>
        <div>
            <div className="controller-panel-content">
                <div className="controller-panel-action d-flex justify-content-center flex-column">
                    <p onClick={() => setIsCollapseMenu(!isCollapseMenu)}>
                        {!isCollapseMenu && (
                            <i className="fa-solid fa-square-caret-left"></i>
                        )}

                        {isCollapseMenu && (
                            <i className="fa-solid fa-square-caret-right"></i>
                        )}
                    </p>
                </div>

                <p className="controller-panel-title">Configuration</p>

                <hr/>

                <div className="controller-step-list">
                    <p data-step-save={1} onClick={handleLinkStep}
                       className={validateStep[0] ? 'panel-step-checked' : ''}>
                        <span>
                            <b>Step 1 :</b> Map configuration
                        </span>

                        {props.configStep === 1 && (
                            <span className="controller-current-page">
                                <i className="fa-solid fa-chevron-right"></i>
                            </span>
                        )}
                    </p>
                </div>

                <hr/>

                <div className="controller-step-list">
                    <p data-step-save={2} onClick={handleLinkStep}
                       className={validateStep[1] ? 'panel-step-checked' : ''}>
                        <span>
                            <b>Step 2 :</b> Zones configuration
                        </span>

                        {props.configStep === 2 && (
                            <span className="controller-current-page">
                                <i className="fa-solid fa-chevron-right"></i>
                            </span>
                        )}
                    </p>
                </div>

                <hr/>

                <div className="controller-step-list">
                    <p data-step-save={3} onClick={handleLinkStep}
                       className={validateStep[2] ? 'panel-step-checked' : ''}>
                        <span>
                            <b>Step 3 :</b> Points configuration
                        </span>

                        {props.configStep === 3 && (
                            <span className="controller-current-page">
                                <i className="fa-solid fa-chevron-right"></i>
                            </span>
                        )}
                    </p>
                </div>

                <hr/>

                <div className="controller-step-list">
                    <p data-step-save={4} onClick={handleLinkStep}
                       className={validateStep[3] ? 'panel-step-checked' : ''}>
                        <span>
                            <b>Step 4 :</b> Gain category setup
                        </span>

                        {props.configStep === 4 && (
                            <span className="controller-current-page">
                                <i className="fa-solid fa-chevron-right"></i>
                            </span>
                        )}
                    </p>
                </div>

                <hr/>

                <div className="controller-step-list">
                    <p data-step-save={5} onClick={handleLinkStep}
                       className={validateStep[4] ? 'panel-step-checked' : ''}>
                        <span>
                            <b>Step 5 :</b> Threat category setup
                        </span>

                        {props.configStep === 5 && (
                            <span className="controller-current-page">
                                <i className="fa-solid fa-chevron-right"></i>
                            </span>
                        )}
                    </p>
                </div>

                <hr/>

                <div className="controller-step-list">
                    <p data-step-save={6} onClick={handleLinkStep}
                       className={validateStep[4] ? 'panel-step-checked' : ''}>
                        <span>
                            <b>Step 6 :</b> Participant session configuration
                        </span>

                        {props.configStep === 6 && (
                            <span className="controller-current-page">
                                <i className="fa-solid fa-chevron-right"></i>
                            </span>
                        )}
                    </p>
                </div>
            </div>
        </div>

    </div>
}
