import React, { useState, useEffect } from 'react';

function NumericInput({ value, onChange, allowDecimal = false, ...props }) {
    const [inputValue, setInputValue] = useState(
        value !== undefined && value !== null && !isNaN(value) ? String(value) : ''
    );

    const handleChange = (e) => {
        const val = e.target.value;

        // Permettre le champ vide
        if (val === '') {
            setInputValue('');
            onChange(e); // Appeler onChange avec l'événement original
            return;
        }

        // Validation en fonction du type de nombre autorisé
        const regex = allowDecimal ? /^-?\d*(\.\d*)?$/ : /^-?\d*$/;
        if (regex.test(val)) {
            setInputValue(val);
            onChange(e); // Appeler onChange avec l'événement original
        }
    };

    // Synchroniser l'état interne si la prop 'value' change de l'extérieur
    useEffect(() => {
        if (value !== undefined && value !== null && value !== '' && !isNaN(value)) {
            setInputValue(String(value));
        } else {
            setInputValue('');
        }
    }, [value]);

    const handleBlur = (e) => {
        const val = e.target.value;

        // Si la valeur est vide, la définir à '0'
        if (val === '') {
            setInputValue('0');
            const syntheticEvent = {
                ...e,
                target: {
                    ...e.target,
                    value: '0'
                }
            };
            onChange(syntheticEvent);
            return;
        }

        // Tenter de convertir la valeur en nombre
        const num = allowDecimal ? parseFloat(val) : parseInt(val, 10);

        // Si la valeur n'est pas un nombre valide, la définir à '0'
        if (isNaN(num)) {
            setInputValue('0');
            const syntheticEvent = {
                ...e,
                target: {
                    ...e.target,
                    value: '0'
                }
            };
            onChange(syntheticEvent);
        }
    };

    return (
        <input
            {...props}
            type="text" // Utiliser 'text' pour permettre les chaînes vides
            value={inputValue}
            onChange={handleChange}
            onBlur={handleBlur}
        />
    );
}

export default NumericInput;
