export class CSVCreator {
    historyGame;
    csvContent;
    configCreator;

    constructor(historyGame, configCreator) {
        this.historyGame = historyGame;
        this.configCreator = configCreator;
    }

    generateCSV() {
        let config = this.configCreator;
        let uniqueInfosParticipant = [];
        //let csvContent = "data:text/csv;charset=utf-8,";
        let csvContent = "";
        csvContent += 'General Stats\r\n';
        csvContent += 'Sessions amount,'
        csvContent += this.historyGame.length + '\r\n\n';

        csvContent += `ID/Times,session,index action,`;
        if(config.infosParticipant) {
            uniqueInfosParticipant = [
                ...new Set(  // éliminer les doublons
                    this.historyGame.flatMap((doc) => {
                        // si infosParticipant existe et est un tableau,
                        // on récupère les labels, sinon on renvoie un tableau vide
                        return Array.isArray(doc.infosParticipant)
                            ? doc.infosParticipant.map((info) => info.label)
                            : [];
                    })
                )
            ];
            uniqueInfosParticipant.forEach((item) => {
                csvContent += `${item},`
            })
        }
        csvContent += config.position ? 'position,' : '';
        csvContent += config.typeAction ? 'Action type,' : '';
        csvContent += config.eventType ? 'Event type,' : '';
        csvContent += config.pointsEvent ? 'Points Earn/Lost,' : '';
        csvContent += config.score ? 'Score,' : '';
        csvContent += config.actionPoints ? 'Action points,' : '';
        csvContent += config.spentTotalTime ? `Total spent time,` : '';

        csvContent += `\r\n`;

        this.historyGame.map((item) => {
            item.sessions.map((sessionRow, indexSession) => {
                sessionRow.rows.map((actionRow, indexAction) => {
                    // NUMERO EXPERIENCE
                    csvContent += `${item._id} ,`;

                    // NUMERO DE SESSION
                    csvContent += `${indexSession},`;

                    // NUMERO DE L'ACTION
                    csvContent += `${indexAction + 1},`;

                    // INFOS PARTICIPANT
                    if(config.infosParticipant) {

                        uniqueInfosParticipant.forEach((targetLabel) => {
                            const targetInfos = item.infosParticipant.find(i => i.label === targetLabel);
                            csvContent += targetInfos ?
                                `${targetInfos.value},` : ' ,'
                        })
                    }

                    // COORDONNEES
                    if(config.position) {
                        csvContent += `[${actionRow.positionX}-${actionRow.positionY}],`;
                    }

                    // TYPE D'ACTIONS
                    if(config.typeAction) {
                        if(actionRow.typeAction === 'exploration') {
                            csvContent += `move to ${actionRow.direction}`;
                        } else {
                            csvContent += `exploitation`;
                        }
                        csvContent += ',';
                    }

                    // EVENT TYPE
                    if(config.eventType) {
                        csvContent += actionRow.eventType + ',';
                    }

                    // POINTS EARN OR LOST
                    if(config.pointsEvent) {
                        if(actionRow.amountValue) {
                            csvContent += actionRow.amountValue > 0 ? '+' : '';
                            csvContent += actionRow.amountValue;
                        } else {
                            csvContent += '0';
                        }
                        csvContent += ',';
                    }


                    // SCORE
                    if(config.score) {
                        csvContent += `${actionRow.score},`;
                    }

                    // ACTION POINTS
                    if(config.actionPoints) {
                        csvContent += `${actionRow.actionPointsLeft},`;
                    }

                    // SPENT TIME
                    if(config.spentTotalTime) {
                        csvContent += `${item.spentTime},`;
                    }

                    csvContent += `\r\n`;
                })
            });
        });

        return csvContent;
    }
}
