import './AccordionButton.css';

export function AccordionButton(props) {
    function handleOpenMenu() {
        props.openMenu();
    }

    return <div className="accordion-menu-container">
        <button onClick={handleOpenMenu}>
            { props.inputValue ?
                <i className="fa-solid fa-square-caret-down"></i>
                :
                <i className="fa-solid fa-square-caret-up"></i>
            }
        </button>
    </div>
}
